import { Body, Controller, Get, Param, Post, Put } from '@nestjs/common';
import { CreateVisitDto } from './dto/create-visit.dto';
import { VisitDocument } from './schemas/visit.schema';
import { VisitsService } from './visits.service';

@Controller('visits')
export class VisitsController {
  constructor(private readonly visitsService: VisitsService) {}

  @Post('add')
  async create(@Body() visit: CreateVisitDto): Promise<VisitDocument> {
    return this.visitsService.insert(visit);
  }

  @Put('update/:id')
  async update(@Body() visit: CreateVisitDto, @Param('id') id: string): Promise<VisitDocument> {
      return this.visitsService.update(visit, id);
  }

  @Get('user/:id')
  async getUserVisits(@Param('id') id: string) {
    return this.visitsService.getUserVisits(id);
  }
}
