import { Injectable, Logger } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Cron } from '@nestjs/schedule';
import { Model } from 'mongoose';
import { ArticlesService } from '../endpoints/articles/article.service';
import {
  Article,
  ArticleDocument,
} from '../endpoints/articles/schemas/article.schema';
import { BookmarksService } from '../endpoints/bookmarks/bookmarks.service';
import { FollowsService } from '../endpoints/follows/follows.service';
import { LikesService } from '../endpoints/likes/likes.service';
import { SharesService } from '../endpoints/shares/shares.service';
import { SkipsService } from '../endpoints/skips/skips.service';
import { StopsService } from '../endpoints/stops/stops.service';
import { VotesService } from '../endpoints/votes/votes.service';
import { WeightDocument } from '../endpoints/weights/schemas/weight.schema';
import { WeightsService } from '../endpoints/weights/weights.service';

interface CalculateData {
  votingCount: number;
  shareCount: number;
  likeCount: number;
  saveCount: number;
  stopCount: number;
  skipCount: number;
  age: number;
  article_id: string;
}
/**
 * baseScore = biasFactor * (postiveBiasCount + negativeBiasCount) + shareFactor * shareCount + likeFactor * likeCount + stopFactor * stopSecondsTotal + saveFactor * saveCount - skipFactor * skipCount;
 * calculatedScore = Math.round((baseScore / Math.pow(ageInHours + 2, timeDecayFactor)) * 1000000) / 1000000;
 */
@Injectable()
export class ScoreCalculatorService {
  constructor(
    private readonly weightsService: WeightsService,
    @InjectModel(Article.name) private articleModel: Model<ArticleDocument>,
    private readonly articlesService: ArticlesService,
    private readonly bookmarksService: BookmarksService,
    private readonly likesService: LikesService,
    private readonly sharesService: SharesService,
    private readonly votesService: VotesService,
    private readonly skipsService: SkipsService,
    private readonly stopsService: StopsService
  ) {}

  @Cron('5 * * * * *')
  async calculateScore() {
    try {
      Logger.log('Calculating score...');
      /** Get scoring weights */
      const weights = await this.weightsService.getWeights();
      /** Set bulk operation */
      const bulkOperation = this.articleModel.collection.initializeUnorderedBulkOp();
      /** Get articles and init data array */
      const articles = await this.articlesService.findAll();
      /** Get all data */
      const bookmarks = await this.bookmarksService.findAndCountByArticle();
      const likes = await this.likesService.findAndCountByArticle();
      const shares = await this.sharesService.findAndCountByArticle();
      const votes = await this.votesService.findAndCountByArticle();
      const skips = await this.skipsService.findAndCountByArticle();
      const stops = await this.stopsService.findAndCountByArticle();
      articles.forEach((article) => {
        let data = {
          votingCount: 1,
          shareCount: 1,
          likeCount: 1,
          saveCount: 1,
          stopCount: 1,
          skipCount: 1,
          age: 0,
          article_id: null,
        } as CalculateData;
        bookmarks
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.saveCount = data.saveCount + result.count;
          });
        likes
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.likeCount = data.likeCount + result.count;
          });
        shares
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.shareCount = data.shareCount + result.count;
          });
        votes
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.votingCount = data.votingCount + result.count;
          });
        skips
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.skipCount = data.skipCount + result.count;
          });
        stops
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.stopCount = data.skipCount + result.count;
          });
        data.article_id = article._id.toString();
        data.age = Math.round(
          (Date.now() - article.publishedAt.getTime()) / 1000 / 3600
        );
        const baseScore =
          weights.bookmarkFactor * data.saveCount +
          weights.voteFactor * data.votingCount +
          weights.shareFactor * data.shareCount +
          weights.stopFactor * data.stopCount +
          weights.likeFactor * data.likeCount -
          weights.skipFactor * data.skipCount -
          0.0001 * data.age;
        const calculated_score =
          Math.round(
            (baseScore / Math.pow(data.age + 2, weights.timeFactor)) * 1000000
          ) / 1000000;
        bulkOperation
          .find({ _id: article._id })
          .update({ $set: { score: calculated_score } });
      });
      await bulkOperation.execute();
      Logger.log('Scores updated...');
      this.calculateTrendingScore(articles, weights);
    } catch (error) {
      Logger.error(error);
    }
  }

  async calculateTrendingScore(
    articles: ArticleDocument[],
    weights: WeightDocument
  ) {
    try {
      Logger.log('Calculating TRENDING score...');
      const bulkOperation = this.articleModel.collection.initializeUnorderedBulkOp();
      const bookmarks = await this.bookmarksService.findAndCountTrending();
      const likes = await this.likesService.findAndCountTrending();
      const shares = await this.sharesService.findAndCountTrending();
      const votes = await this.votesService.findAndCountTrending();
      const skips = await this.skipsService.findAndCountTrending();
      const stops = await this.stopsService.findAndCountTrending();
      articles.forEach((article) => {
        let data = {
          votingCount: 1,
          shareCount: 1,
          likeCount: 1,
          saveCount: 1,
          stopCount: 1,
          skipCount: 1,
          age: 0,
          article_id: null,
        } as CalculateData;
        bookmarks
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.saveCount = data.saveCount + result.count;
          });
        likes
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.likeCount = data.likeCount + result.count;
          });
        shares
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.shareCount = data.shareCount + result.count;
          });
        votes
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.votingCount = data.votingCount + result.count;
          });
        skips
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.skipCount = data.skipCount + result.count;
          });
        stops
          .filter(
            (val) => val._id.article.toString() === article._id.toString()
          )
          .forEach((result) => {
            data.stopCount = data.skipCount + result.count;
          });
        data.article_id = article._id.toString();
        data.age = Math.round(
          (Date.now() - article.publishedAt.getTime()) / 1000 / 3600
        );
        const baseScore =
          weights.bookmarkFactor * data.saveCount +
          weights.voteFactor * data.votingCount +
          weights.shareFactor * data.shareCount +
          weights.stopFactor * data.stopCount +
          weights.likeFactor * data.likeCount -
          weights.skipFactor * data.skipCount -
          0.0001 * data.age;
        bulkOperation
          .find({ _id: article._id })
          .update({ $set: { trendingScore: baseScore } });
      });
      await bulkOperation.execute();
      Logger.log('Trending scores updated...');
    } catch (error) {
      Logger.error(error);
    }
  }
}
