import { Injectable } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Aggregate, Model, Types } from 'mongoose';
import { CountArticle } from '../../interfaces/count-article';
import { CreateVoteDto } from './dto/create-vote.dto';
import { Vote, VoteDocument } from './schemas/vote.schema';

@Injectable()
export class VotesService {
  constructor(@InjectModel(Vote.name) private voteModel: Model<VoteDocument>) {}

  async findAll(): Promise<VoteDocument[]> {
    return this.voteModel.find().exec();
  }

  async findOne(id: string): Promise<VoteDocument> {
    return this.voteModel.findById(id).exec();
  }

  async updateOne(id: string, value: boolean): Promise<VoteDocument> {
    return this.voteModel.updateOne({_id: Types.ObjectId(id)}, {value: value}).exec();
  }

  async findByArticle(id: string): Promise<VoteDocument[]> {
    return this.voteModel.find({article: Types.ObjectId(id)}).exec();
  }

  async findByUser(id: string): Promise<VoteDocument[]> {
    return this.voteModel.find({user: Types.ObjectId(id)}).exec();
  }

  async getResultsFromArticle(id: string) {
    const articles = await this.voteModel.find({article: Types.ObjectId(id)}).exec();
    let yes = [];
    let no = [];
    articles.forEach((article) => {
      if(article.value) {
        yes.push(1)
      } else {
        no.push(0);
      }
    });
    let result = {
      yes: Math.round((yes.length / (articles.length) ) * 100),
      no: Math.round((no.length / (articles.length) ) * 100),
    }
    return result;
  }

  async delete(id: string): Promise<void> {
    return this.voteModel.deleteOne({ _id: id }).exec();
  }

  async insert(vote: CreateVoteDto): Promise<VoteDocument> {
    let v = {
      article: Types.ObjectId(vote.article),
      user: Types.ObjectId(vote.user),
      value: vote.value
    };
    return this.voteModel.create(v);
  }

  async insertMany(votes: Array<VoteDocument>): Promise<any> {
    return this.voteModel.insertMany(votes);
  }

  async deleteAll(): Promise<any> {
    return this.voteModel.remove({}).exec();
  }

  findAndCountByArticle(): Aggregate<CountArticle[]> {
    const aggregatorOpts = [
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      },
    ];
    return this.voteModel.aggregate(aggregatorOpts);
  }

  findAndCountTrending(): Aggregate<CountArticle[]> {
    const now = new Date(Date.now());
    now.setHours(now.getHours() - 1);
    const aggregatorOpts = [
      {
        $match: {
          createdAt: { $gte: now}
        }
      },
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      }
    ];
    return this.voteModel.aggregate(aggregatorOpts);
  }
}
