import { Prop, Schema, SchemaFactory } from '@nestjs/mongoose';
import { Document } from 'mongoose';

export type SourceDocument = Source & Document;

@Schema()
export class Source {
  @Prop()
  name: string;

  @Prop({ unique: true })
  url: string;

  @Prop()
  logoUrl: string;
}

export const SourceSchema = SchemaFactory.createForClass(Source);
