import { forwardRef, Inject, Injectable, Logger } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model } from 'mongoose';
import { CreateSourceDto } from './dto/create-source.dto';
import { Source, SourceDocument } from './schemas/source.schema';
@Injectable()
export class SourcesService {
  constructor(
    @InjectModel(Source.name) private sourceModel: Model<SourceDocument>
  ) {}

  async findAll(): Promise<SourceDocument[]> {
    return this.sourceModel.find().exec();
  }

  async delete(id: string): Promise<void> {
    return this.sourceModel.deleteOne({ _id: id }).exec();
  }

  async insert(source: CreateSourceDto): Promise<SourceDocument> {
    return this.sourceModel.create(source);
  }

  async insertMany(sources: Array<SourceDocument>): Promise<any> {
    return this.sourceModel.insertMany(sources, { ordered: false });
  }

  async deleteAll(): Promise<any> {
    return this.sourceModel.remove({}).exec();
  }
}
