import { Logger, Module, OnModuleInit } from '@nestjs/common';
import { InjectConnection, MongooseModule } from '@nestjs/mongoose';
import { Connection } from 'mongoose';
import { Weight, WeightSchema } from './schemas/weight.schema';
import { WeightsService } from './weights.service';

@Module({
  imports: [
    MongooseModule.forFeature([{ name: Weight.name, schema: WeightSchema }]),
  ],
  providers: [WeightsService],
  exports: [WeightsService, MongooseModule],
})
export class WeightsModule implements OnModuleInit {
  constructor(
    private weightsService: WeightsService,
    @InjectConnection() private readonly mongoConnection: Connection
  ) {}

  async onModuleInit() {
    try {
      const response = await this.mongoConnection.db
        .listCollections({ name: 'weights' })
        .next();
      if (response === null) {
        await this.weightsService.initWeights();
        Logger.log('Weights collection initialized...');
      }
    } catch (error) {
      Logger.error('Something happened listing weight mongo collection', error);
    }
  }
}
