import { Injectable } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model, Types, Aggregate } from 'mongoose';
import { CountArticle } from '../../interfaces/count-article';
import { CreateSkipDto } from './dto/create-skip.dto';
import { Skip, SkipDocument } from './schemas/skip.schema';

@Injectable()
export class SkipsService {
  constructor(
    @InjectModel(Skip.name) private skipModel: Model<SkipDocument>
  ) {}

  async insert(skip: CreateSkipDto): Promise<SkipDocument> {
    let article = Types.ObjectId(skip.article);
    let user = Types.ObjectId(skip.user);
    return this.skipModel.create({ article: article, user: user });
  }

  async getUserSkips(user_id: string): Promise<SkipDocument[]> {
    return this.skipModel
      .find({})
      .where('user')
      .equals(Types.ObjectId(user_id))
      .exec();
  }

  async findAll(): Promise<SkipDocument[]> {
    return this.skipModel.find({}).exec();
  }

  findAndCountByArticle(): Aggregate<CountArticle[]> {
    const aggregatorOpts = [
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      },
    ];
    return this.skipModel.aggregate(aggregatorOpts);
  }

  findAndCountTrending(): Aggregate<CountArticle[]> {
    const now = new Date(Date.now());
    now.setHours(now.getHours() - 1);
    const aggregatorOpts = [
      {
        $match: {
          createdAt: { $gte: now}
        }
      },
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      }
    ];
    return this.skipModel.aggregate(aggregatorOpts);
  }
}
