import { Injectable } from '@nestjs/common';
import { CreateBookmarkDto } from './dto/create-bookmark.dto';
import { Bookmark, BookmarkDocument } from './schemas/bookmark.schema';
import { InjectModel } from '@nestjs/mongoose';
import { Aggregate, Model, Types } from 'mongoose';
import { CountArticle } from '../../interfaces/count-article';
@Injectable()
export class BookmarksService {
  constructor(
    @InjectModel(Bookmark.name) private bookmarkModel: Model<BookmarkDocument>
  ) {}

  async insert(bookmark: CreateBookmarkDto): Promise<BookmarkDocument> {
    let article = Types.ObjectId(bookmark.article);
    let user = Types.ObjectId(bookmark.user);
    return this.bookmarkModel.create({ article: article, user: user });
  }

  async delete(id: string): Promise<void> {
    return this.bookmarkModel.deleteOne({ _id: id }).exec();
  }

  async getUserBookmarks(user_id: string): Promise<BookmarkDocument[]> {
    return this.bookmarkModel
      .find({})
      .where('user')
      .equals(Types.ObjectId(user_id))
      .exec();
  }

  async findAll(): Promise<BookmarkDocument[]> {
    return this.bookmarkModel.find({}).exec();
  }

  findAndCountByArticle(): Aggregate<CountArticle[]> {
    const aggregatorOpts = [
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      },
    ];
    return this.bookmarkModel.aggregate(aggregatorOpts);
  }

  findAndCountTrending(): Aggregate<CountArticle[]> {
    const now = new Date(Date.now());
    now.setHours(now.getHours() - 1);
    const aggregatorOpts = [
      {
        $match: {
          createdAt: { $gte: now}
        }
      },
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      }
    ];
    return this.bookmarkModel.aggregate(aggregatorOpts);
  }
}
