import { Injectable } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Aggregate, Model, Types } from 'mongoose';
import { CountArticle } from '../../interfaces/count-article';
import { CreateLikeDto } from './dto/create-like.dto';
import { Like, LikeDocument } from './schemas/like.schema';
@Injectable()
export class LikesService {
  constructor(@InjectModel(Like.name) private likeModel: Model<LikeDocument>) {}

  async insert(like: CreateLikeDto): Promise<LikeDocument> {
    let article = Types.ObjectId(like.article);
    let user = Types.ObjectId(like.user);
    return this.likeModel.create({ article: article, user: user });
  }

  async delete(id: string): Promise<void> {
    return this.likeModel.deleteOne({ _id: id }).exec();
  }

  async getUserLikes(user_id: string): Promise<LikeDocument[]> {
    return this.likeModel
      .find({})
      .where('user')
      .equals(Types.ObjectId(user_id))
      .exec();
  }

  async findAll(): Promise<LikeDocument[]> {
    return this.likeModel.find({}).exec();
  }

  findAndCountByArticle(): Aggregate<CountArticle[]> {
    const aggregatorOpts = [
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      },
    ];
    return this.likeModel.aggregate(aggregatorOpts);
  }

  findAndCountTrending(): Aggregate<CountArticle[]> {
    const now = new Date(Date.now());
    now.setHours(now.getHours() - 1);
    const aggregatorOpts = [
      {
        $match: {
          createdAt: { $gte: now}
        }
      },
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      }
    ];
    return this.likeModel.aggregate(aggregatorOpts);
  }
}
