import { Injectable } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Aggregate, Model, Types } from 'mongoose';
import { CountArticle } from '../../interfaces/count-article';
import { CreateFollowDto } from './dto/create-follow.dto';
import { Follow, FollowDocument } from './schemas/follow.schema';

@Injectable()
export class FollowsService {
  constructor(
    @InjectModel(Follow.name) private followModel: Model<FollowDocument>
  ) {}

  async insert(follow: CreateFollowDto): Promise<FollowDocument> {
    let source = Types.ObjectId(follow.source);
    let user = Types.ObjectId(follow.user);
    return this.followModel.create({ source: source, user: user });
  }

  async delete(id: string): Promise<void> {
    return this.followModel.deleteOne({ _id: id }).exec();
  }

  async getUserFollows(user_id: string): Promise<FollowDocument[]> {
    return this.followModel
      .find({})
      .where('user')
      .equals(Types.ObjectId(user_id))
      .exec();
  }

  async findAll(): Promise<FollowDocument[]> {
    return this.followModel.find({}).exec();
  }
}
