import { Body, Controller, Delete, Get, Param, Post } from '@nestjs/common';
import { CreateFollowDto } from './dto/create-follow.dto';
import { FollowsService } from './follows.service';
import { FollowDocument } from './schemas/follow.schema';

@Controller('follows')
export class FollowsController {
    constructor(private readonly followsService: FollowsService) {}

  @Post('add')
  async create(@Body() follow: CreateFollowDto): Promise<FollowDocument> {
    return this.followsService.insert(follow);
  }

  @Get('user/:id')
  async getUserFollows(@Param('id') id: string): Promise<FollowDocument[]> {
    return this.followsService.getUserFollows(id);
  }

  @Delete(':id')
  async delete(@Param('id') id: string): Promise<void> {
    return this.followsService.delete(id);
  }
}
