import { Body, Controller, Delete, Get, Param, Post } from '@nestjs/common';
import { BookmarksService } from './bookmarks.service';
import { CreateBookmarkDto } from './dto/create-bookmark.dto';
import { BookmarkDocument } from './schemas/bookmark.schema';

@Controller('bookmarks')
export class BookmarksController {
  constructor(private readonly bookmarksService: BookmarksService) {}

  @Post('add')
  async create(@Body() like: CreateBookmarkDto): Promise<BookmarkDocument> {
    return this.bookmarksService.insert(like);
  }

  @Get('user/:id')
  async getUserLikes(@Param('id') id: string) {
    return this.bookmarksService.getUserBookmarks(id);
  }

  @Delete(':id')
  async delete(@Param('id') id: string): Promise<void> {
    return this.bookmarksService.delete(id);
  }
}
