import { Injectable } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Aggregate, Model, Types } from 'mongoose';
import { CountArticle } from '../../interfaces/count-article';
import { CreateShareDto } from './dto/create-share.dto';
import { Share, ShareDocument } from './schemas/share.schema';

@Injectable()
export class SharesService {
  constructor(
    @InjectModel(Share.name) private shareModel: Model<ShareDocument>
  ) {}

  async insert(share: CreateShareDto): Promise<ShareDocument> {
    let article = Types.ObjectId(share.article);
    let user = Types.ObjectId(share.user);
    return this.shareModel.create({ article: article, user: user });
  }

  async getUserShares(user_id: string): Promise<ShareDocument[]> {
    return this.shareModel
      .find({})
      .where('user')
      .equals(Types.ObjectId(user_id))
      .exec();
  }

  async findAll(): Promise<ShareDocument[]> {
    return this.shareModel.find({}).exec();
  }

  findAndCountByArticle(): Aggregate<CountArticle[]> {
    const aggregatorOpts = [
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      },
    ];
    return this.shareModel.aggregate(aggregatorOpts);
  }

  findAndCountTrending(): Aggregate<CountArticle[]> {
    const now = new Date(Date.now());
    now.setHours(now.getHours() - 1);
    const aggregatorOpts = [
      {
        $match: {
          createdAt: { $gte: now },
        },
      },
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      },
    ];
    return this.shareModel.aggregate(aggregatorOpts);
  }
}
