import { Injectable } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model } from 'mongoose';
import { BookmarksService } from '../bookmarks/bookmarks.service';
import { FollowsService } from '../follows/follows.service';
import { Follow, FollowDocument } from '../follows/schemas/follow.schema';
import { CreateArticleDto } from './dto/create-article.dto';
import { Article, ArticleDocument } from './schemas/article.schema';

@Injectable()
export class ArticlesService {
  constructor(
    @InjectModel(Article.name) private articleModel: Model<ArticleDocument>,
    private readonly followsService: FollowsService,
    private readonly bookmarksService: BookmarksService
  ) {}

  async findAll(): Promise<ArticleDocument[]> {
    return this.articleModel.find().exec();
  }

  async findPaginated(page: number, limit: number): Promise<any> {
    try {
      const results = await this.articleModel
        .find()
        .limit(limit)
        .skip((page - 1) * limit)
        .sort({ score: -1 })
        .populate('source')
        .exec();

      const count = await this.articleModel.countDocuments().exec();
      return { results: results, count: count };
    } catch (error) {
      return error;
    }
  }

  async findLatestPaginated(page: number, limit: number): Promise<any> {
    try {
      const results = await this.articleModel
        .find()
        .limit(limit)
        .skip((page - 1) * limit)
        .sort({ publishedAt: -1 })
        .populate('source')
        .exec();

      const count = await this.articleModel.countDocuments().exec();
      return { results: results, count: count };
    } catch (error) {
      return error;
    }
  }

  async findTrendingPaginated(page: number, limit: number): Promise<any> {
    try {
      const results = await this.articleModel
        .find()
        .limit(limit)
        .skip((page - 1) * limit)
        .sort({ trendingScore: -1 })
        .populate('source')
        .exec();

      const count = await this.articleModel.countDocuments().exec();
      return { results: results, count: count };
    } catch (error) {
      return error;
    }
  }

  async insert(article: CreateArticleDto): Promise<ArticleDocument> {
    return this.articleModel.create(article);
  }

  async insertMany(articles: Array<ArticleDocument>): Promise<any> {
    return this.articleModel.insertMany(articles, { ordered: false });
  }

  async delete(id: string): Promise<void> {
    return this.articleModel.deleteOne({ _id: id }).exec();
  }

  async deleteAll(): Promise<void> {
    return this.articleModel.remove({}).exec();
  }

  async findFollowedArticlesPaginated(
    id: string,
    page: number,
    limit: number
  ): Promise<any> {
    const follows = await this.followsService.getUserFollows(id);
    const queryArray = [];
    follows.forEach((follow) => {
      queryArray.push({ source: follow.source });
    });
    if (queryArray.length > 0) {
      const results = await this.articleModel
        .find({ $or: queryArray })
        .limit(limit * 1)
        .skip((page - 1) * 20)
        .sort({ publishedAt: -1 })
        .populate('source')
        .exec();
        const count = await this.articleModel.countDocuments().exec();
        return { results: results, count: count}
    } else {
      return { results: [], count: 0};
    }
  }

  async findSavedArticles(id: string) {
    const bookmarks = await this.bookmarksService.getUserBookmarks(id);
    const queryArray = [];
    bookmarks.forEach((bookmark) => {
      queryArray.push({ _id: bookmark.article });
    });
    if (queryArray.length > 0) {
      const results = await this.articleModel
        .find({ $or: queryArray })
        .sort({ publishedAt: -1 })
        .populate('source')
        .exec();
        return results;
    } else {
      return [];
    }
  }
}
