import { Body, Controller, Delete, Get, Param, Post } from '@nestjs/common';
import { CreateLikeDto } from './dto/create-like.dto';
import { LikesService } from './likes.service';
import { LikeDocument } from './schemas/like.schema';

@Controller('likes')
export class LikesController {
  constructor(private readonly likesService: LikesService) {}

  @Post('add')
  async create(@Body() like: CreateLikeDto): Promise<LikeDocument> {
    return this.likesService.insert(like);
  }

  @Get('user/:id')
  async getUserLikes(@Param('id') id: string) {
    return this.likesService.getUserLikes(id);
  }

  @Delete(':id')
  async delete(@Param('id') id: string): Promise<void> {
    return this.likesService.delete(id);
  }
}
