import { Body, Controller, Get, Logger, Param, Post, Put, Query } from '@nestjs/common';
import { CreateVoteDto } from './dto/create-vote.dto';
import { Vote, VoteDocument } from './schemas/vote.schema';
import { VotesService } from './votes.service';

@Controller('votes')
export class VotesController {
  constructor(private readonly votesService: VotesService) {}

  @Get()
  async findAll(): Promise<Vote[]> {
    return this.votesService.findAll();
  }

  @Put('update')
  async updateVote(@Body() data: any): Promise<VoteDocument> {
    return this.votesService.updateOne(data.id.toString(), data.value);
  }

  @Post('add')
  async insertOne(@Body() createVoteDto: CreateVoteDto): Promise<any> {
      return this.votesService.insert(createVoteDto);
  }

  @Get('user')
  async getVotesFromUser(@Query() query: any) {
    return this.votesService.findByUser(query.id.toString());
  }

  @Get('article/:id')
  async findByArticle(@Param('id') id: string): Promise<VoteDocument[]> {
    return this.votesService.findByArticle(id);
  }

  @Get('results/article/:id')
  async getResultsFromArticle(@Param('id') id: string): Promise<any> {
    return this.votesService.getResultsFromArticle(id);
  }
}
