import { Body, Controller, Get, Param, Post } from '@nestjs/common';
import { CreateSkipDto } from './dto/create-skip.dto';
import { SkipDocument } from './schemas/skip.schema';
import { SkipsService } from './skips.service';

@Controller('skips')
export class SkipsController {
    constructor(private readonly skipsServices: SkipsService) {}

  @Post('add')
  async create(@Body() skip: CreateSkipDto): Promise<SkipDocument> {
    return this.skipsServices.insert(skip);
  }

  @Get('user/:id')
  async getUserSkips(@Param('id') id: string) {
    return this.skipsServices.getUserSkips(id);
  }
}
