import { Body, Controller, Delete, Get, Param, Post } from '@nestjs/common';
import { CreateShareDto } from './dto/create-share.dto';
import { ShareDocument } from './schemas/share.schema';
import { SharesService } from './shares.service';

@Controller('shares')
export class SharesController {
  constructor(private readonly sharesService: SharesService) {}

  @Post('add')
  async create(@Body() share: CreateShareDto): Promise<ShareDocument> {
    return this.sharesService.insert(share);
  }

  @Get('user/:id')
  async getUserShares(@Param('id') id: string) {
    return this.sharesService.getUserShares(id);
  }
}
