import { Body, Controller, Get, Logger, Param, Post, Put, Query } from '@nestjs/common';
import { RolesService } from '../roles/roles.service';
import { CreateUserDto } from './dto/create-user.dto';
import { UserDocument } from './schemas/user.schema';
import { UsersService } from './users.service';

@Controller('users')
export class UsersController {
  constructor(
    private readonly usersService: UsersService,
    private readonly rolesService: RolesService
  ) {}

  @Post('add')
  async addUser(@Body() user: CreateUserDto): Promise<UserDocument> {
    const clientRole = await this.rolesService.getClientRole();
    user.roles.push(clientRole._id);
    return this.usersService.insert(user);
  }

  @Get(':id')
  async getUser(@Param('id') id: string): Promise<UserDocument> {
    return this.usersService.findOne(id);
  }

  @Put('update/:id')
  async updateUser(@Param('id') id: string, @Body() data: any): Promise<UserDocument> {
    return this.usersService.updateUser(id, data);
  }

  @Post('check')
  async checkUser(@Body() data: any) {
    try {
      const first = await this.usersService.findByIpAddress(data.ipAddress);
      const second = await this.usersService.findByUniqueToken(data.token);
      if (first !== null || second !== null) {
        if (first) {
          return { value: true, user: first };
        } else {
          return { value: true, user: second };
        }
      } else {
        return { value: false, user: null };
      }
    } catch (error) {
      Logger.error('Something ocurred checking user...', error);
    }
  }

  @Get('sign-check/:uid')
  async checkIfRegistered(@Param('uid') uid: string) {
    try {
      return this.usersService.findByAuthUid(uid.toString());
    } catch (error) {
      Logger.error('Something ocurred checking for auth uid in user...', error);
    }
  }

  @Get('email/check')
  async checkEmail(@Query('email') email: string) {
    try {
      return this.usersService.findByEmail(email.toString());
    } catch (error) {
      Logger.error('Something ocurred checking for email in user...', error);
    }
  }
}
