import { Injectable } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model, Types, Aggregate } from 'mongoose';
import { CountArticle } from '../../interfaces/count-article';
import { CreateVisitDto } from './dto/create-visit.dto';
import { Visit, VisitDocument } from './schemas/visit.schema';

@Injectable()
export class VisitsService {
  constructor(
    @InjectModel(Visit.name) private visitModel: Model<VisitDocument>
  ) {}

  async insert(visit: CreateVisitDto): Promise<VisitDocument> {
    let article = Types.ObjectId(visit.article);
    let user = Types.ObjectId(visit.user);
    return this.visitModel.create({ article: article, user: user, amount: 1 });
  }

  async update(visit: CreateVisitDto, id: string): Promise<VisitDocument> {
    return this.visitModel.updateOne(
      { _id: id },
      { amount: visit.amount }
    );
  }

  async getUserVisits(user_id: string): Promise<VisitDocument[]> {
    return this.visitModel
      .find({})
      .where('user')
      .equals(Types.ObjectId(user_id))
      .exec();
  }

  async findAll(): Promise<VisitDocument[]> {
    return this.visitModel.find({}).exec();
  }

  findAndCountByArticle(): Aggregate<CountArticle[]> {
    const aggregatorOpts = [
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      },
    ];
    return this.visitModel.aggregate(aggregatorOpts);
  }
}
