import { Injectable } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model } from 'mongoose';
import { Role, RoleDocument } from './schemas/role.schema';

@Injectable()
export class RolesService {
  constructor(@InjectModel(Role.name) private roleModel: Model<RoleDocument>) {}

  async initRoles(): Promise<any> {
    return this.roleModel.insertMany([
      { value: 'CLIENT' } as RoleDocument,
      { value: 'ADMIN' } as RoleDocument,
    ]);
  }

  async getClientRole(): Promise<RoleDocument> {
    return this.roleModel.findOne({ value: 'CLIENT' }).exec();
  }
}
