import { Body, Controller, Get, Param, Post } from '@nestjs/common';
import { CreateStopDto } from './dto/create-stop.dto';
import { StopDocument } from './schemas/stop.schema';
import { StopsService } from './stops.service';

@Controller('stops')
export class StopsController {
  constructor(private readonly stopsService: StopsService) {}

  @Post('add')
  async create(@Body() stop: CreateStopDto): Promise<StopDocument> {
    return this.stopsService.insert(stop);
  }

  @Get('user/:id')
  async getUserStops(@Param('id') id: string) {
    return this.stopsService.getUserStops(id);
  }
}
