import { Injectable } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model, Types, Aggregate } from 'mongoose';
import { CountArticle } from '../../interfaces/count-article';
import { CreateStopDto } from './dto/create-stop.dto';
import { Stop, StopDocument } from './schemas/stop.schema';

@Injectable()
export class StopsService {
  constructor(@InjectModel(Stop.name) private stopModel: Model<StopDocument>) {}

  async insert(stop: CreateStopDto): Promise<StopDocument> {
    let article = Types.ObjectId(stop.article);
    let user = Types.ObjectId(stop.user);
    return this.stopModel.create({ article: article, user: user });
  }

  async getUserStops(user_id: string): Promise<StopDocument[]> {
    return this.stopModel
      .find({})
      .where('user')
      .equals(Types.ObjectId(user_id))
      .exec();
  }

  async findAll(): Promise<StopDocument[]> {
    return this.stopModel.find({}).exec();
  }

  findAndCountByArticle(): Aggregate<CountArticle[]> {
    const aggregatorOpts = [
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      },
    ];
    return this.stopModel.aggregate(aggregatorOpts);
  }

  findAndCountTrending(): Aggregate<CountArticle[]> {
    const now = new Date(Date.now());
    now.setHours(now.getHours() - 1);
    const aggregatorOpts = [
      {
        $match: {
          createdAt: { $gte: now },
        },
      },
      {
        $group: {
          _id: { article: '$article' },
          count: { $sum: 1 },
        },
      },
    ];
    return this.stopModel.aggregate(aggregatorOpts);
  }
}
