import { Logger, Module, OnModuleInit } from '@nestjs/common';
import { AppController } from './app.controller';
import { AppService } from './app.service';
import { InjectConnection, MongooseModule } from '@nestjs/mongoose';
import { CronJobsModule } from './cron-jobs/cron.module';
import { ScheduleModule } from '@nestjs/schedule';
import { EndpointsModule } from './endpoints/endpoints.module';
import { RolesService } from './endpoints/roles/roles.service';
import { Connection } from 'mongoose';
import { ScoreCalculatorModule } from './score-calculator/score-calculator.module';
import { ConfigModule } from '@nestjs/config';

// mongodb+srv://genzosolutions:<password>@cluster0.pgnb2.mongodb.net/myFirstDatabase?retryWrites=true&w=majority
@Module({
  imports: [
    ConfigModule.forRoot(),
    MongooseModule.forRoot(
      process.env.MONGO_URL,
      {
        useCreateIndex: true
      }
    ),
    EndpointsModule,
    CronJobsModule,
    ScheduleModule.forRoot(),
    ScoreCalculatorModule,
  ],
  controllers: [AppController],
  providers: [AppService],
})
export class AppModule implements OnModuleInit {
  constructor(
    private rolesService: RolesService,
    @InjectConnection() private readonly mongoConnection: Connection
  ) {}

  async onModuleInit() {
    try {
      const response = await this.mongoConnection.db
        .listCollections({ name: 'roles' })
        .next();
      if (response === null) {
        await this.rolesService.initRoles();
        Logger.log('Roles collection initialized...');
      }
    } catch (error) {
      Logger.error('Something happened listing mongo collection', error);
    }
  }
}
