import { HttpModule, Module } from '@nestjs/common';
import { MongooseModule } from '@nestjs/mongoose';
import { ArticlesModule } from '../endpoints/articles/article.module';
import { Article, ArticleSchema } from '../endpoints/articles/schemas/article.schema';
import { Source, SourceSchema } from '../endpoints/sources/schemas/source.schema';
import { SourcesModule } from '../endpoints/sources/sources.module';
import { CronService } from './cron.service';
@Module({
  imports: [
    MongooseModule.forFeature([{ name: Article.name, schema: ArticleSchema }, {name: Source.name, schema: SourceSchema}]),
    HttpModule,
    ArticlesModule,
    SourcesModule
  ],
  controllers: [],
  providers: [CronService],
})
export class CronJobsModule {}